.bottomsheet {
  overflow: hidden;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.bottomsheet__backdrop {
  position: absolute;
  background-color: var(--warna-hitam);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.5;
}
.bottomsheet__isi {
  margin: 0 auto;
  max-width: 75rem;
  overflow: hidden;
  position: absolute;
  background-color: var(--warna-putih);
  left: 0;
  right: 0;
  bottom: 0;
  top: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1.25rem 1.25rem 0 0;
}
.bottomsheet__isi h1 {
  position: relative;
}